<template>
	<div
		class="farm-record"
		v-title
		data-title="生产作业上报统计"
		v-loading="excelLoading"
		element-loading-text="数据导出中..."
		element-loading-spinner="el-icon-loading"
		element-loading-background="rgba(0, 0, 0, 0.5)"
	>
		<Nav @back="back" title="生产作业上报统计" :hasShadow="true" backColor="#B6CFDE" paddingRight="13px" :isUserCenter="true" :isAddNum="isAddNum">
			<!-- <div slot="right" style="width: 246px; height: 55px; position: relative;">
				<UserCenter></UserCenter>
			</div> -->
		</Nav>

		<div class="main">
			<div class="search-box">
				<div class="search-box-left">
					<CustomSelect margin="0 15px 0 0">
						<template slot="select-container">
							<el-select v-model="businessCategory" placeholder="选择经营类目" @change="businessChange">
								<el-option v-for="item in businessCategoryList" :key="item.id" :label="item.bizCategoryName" :value="item.bizCategoryName"> </el-option>
							</el-select>
						</template>
					</CustomSelect>

					<CustomDatePicker margin="0 15px 0 0" :clearable="false" v-model="dateValue" :pickerOptions="pickerOptions" @change="dateChange" @focus="focus"></CustomDatePicker>

					<CustomAreaSelect @areaCilck="areaCilck" :level="level" :areaId="startId"></CustomAreaSelect>
				</div>
				<div class="excel-box">
					<CustomInput iconfont="el-icon-search" placeholder="输入经营主体名称搜索" v-model="keyword" @clickAndEnter="clickAndEnter"></CustomInput>
					<div class="excel" @click="download">
						<img src="../../assets/images/table/bg-excel-icon.png" />
						<span style="margin-left: 9px">导出EXCEL</span>
					</div>
				</div>
			</div>
			<div class="list-content" ref="listContent">
				<!-- <Loading :show="true" type="element"></Loading> -->
				<CustomTable>
					<template slot="table-container">
						<el-table :data="farmRecordList" style="width: 100%" :fit="true" :height="tableHeight" :lazy="true">
							<template slot="empty">
								<div>
									<Loading :show="loading" type="element" theme="loader-light"></Loading>
									<div class="no-data" v-if="!loading">
										<img src="../../assets/images/table/bg-table-no-data.png" />
										<span>暂无数据</span>
									</div>
								</div>
							</template>
							<el-table-column label="经营主体名称" width="280">
								<template slot-scope="scope">
									<span class="text-bold text-hover" style="font-size: 14px; cursor: pointer" @click="goFarm(scope.row)">{{ scope.row.farmName ? scope.row.farmName : '--' }}</span>
								</template>
							</el-table-column>

							<el-table-column label="所在地">
								<template slot-scope="scope">
									<span class="text">{{ spliceAreaName(scope.row) ? spliceAreaName(scope.row) : '--' }}</span>
								</template>
							</el-table-column>

							<el-table-column label="经营类目">
								<template slot-scope="scope">
									<span class="text">{{ scope.row.categoryNames ? scope.row.categoryNames : '--' }}</span>
								</template>
							</el-table-column>

							<el-table-column label="在田品种">
								<template slot-scope="scope">
									<span class="text text-ellipsis" v-if="!scope.row.cropNames">--</span>
									<!-- popper-class="farm-record-custom-tooltip-light"白色主题 -->
									<el-tooltip v-else class="item" effect="dark" popper-class="farm-record-custom-tooltip-dark" placement="top">
										<div slot="content" class="tooltip-box">{{ scope.row.cropNames }}</div>
										<span class="text text-ellipsis" @click="buttonClick">{{ scope.row.cropNames ? scope.row.cropNames : '--' }}</span>
									</el-tooltip>
								</template>
							</el-table-column>

							<!-- <el-table-column
								width="210"
							>
								<template slot="header" slot-scope="scope">
									<CustomTableSort title="认证地块面积（亩）" prop="landCertArea" :defaultSort="defaultSort" @sortChange="sortChange"></CustomTableSort>
								</template>
								<template slot-scope="scope">
									<span class="text-bold">{{ scope.row.landCertArea }}</span>
								</template>
							</el-table-column> -->

							<el-table-column label="农事填报记录(条)" align="center">
								<el-table-column width="200" align="center">
									<template slot="header" slot-scope="scope">
										<span class="custom-header-text">添加种/养殖档案</span>
									</template>
									<template slot-scope="scope">
										<span class="text-bold text-blue">{{ scope.row.planCounts }}</span>
									</template>
								</el-table-column>
								<el-table-column label="施肥" align="center">
									<template slot="header" slot-scope="scope">
										<span class="custom-header-text">施肥</span>
									</template>
									<template slot-scope="scope">
										<span class="text-bold text-blue">{{ scope.row.flCounts }}</span>
									</template>
								</el-table-column>
								<el-table-column label="用药" align="center">
									<template slot="header" slot-scope="scope">
										<span class="custom-header-text">用药</span>
									</template>
									<template slot-scope="scope">
										<span class="text-bold text-blue">{{ scope.row.nyCounts }}</span>
									</template>
								</el-table-column>
								<el-table-column align="center">
									<template slot="header" slot-scope="scope">
										<span class="custom-header-text">采收/捕捞/出栏</span>
									</template>
									<template slot-scope="scope">
										<span class="text-bold text-blue">{{ scope.row.recordCounts }}</span>
									</template>
								</el-table-column>
								<el-table-column align="center">
									<template slot="header" slot-scope="scope">
										<span class="custom-header-text">其他</span>
									</template>
									<template slot-scope="scope">
										<span class="text-bold text-blue">{{ scope.row.otherCounts }}</span>
									</template>
								</el-table-column>
								<el-table-column align="center">
									<template slot="header" slot-scope="scope">
										<span class="custom-header-text">合计</span>
									</template>
									<!-- <template slot="header" slot-scope="scope">
										<CustomTableSort title="合计" align="center" :titleStyle="titleStyle" :defaultSort="defaultSort" @sortChange="sortChange"></CustomTableSort>
									</template> -->
									<template slot-scope="scope">
										<span class="text-bold text-green">{{ scope.row.counts }}</span>
									</template>
								</el-table-column>
							</el-table-column>
						</el-table>
					</template>
				</CustomTable>
			</div>
		</div>

		<div v-if="total > pageSize" class="page-box">
			<Page
				:total="total"
				:page-size="pageSize"
				:current="currentPage"
				@onChange="onChange"
				@changePageSize="changePageSize"
				:isPageSizeShow="true"
				:isJumpShow="true"
				:isTotalShow="true"
				:perPage="15"
			></Page>
		</div>

		<RunBall :isRunBall="isRunBall"></RunBall>

		<downloadModal :isShow.sync="isModalShow"></downloadModal>
	</div>
</template>

<script>
import Nav from '@/components/common/nav.vue';
import Page from '@/components/common/page.vue';
import Loading from '@/components/common/loading.vue';
import CustomInput from '@/components/common/input.vue';
import CustomSelect from '@/components/common/select.vue';
import CustomTable from '@/components/common/table.vue';
import CustomTableSort from '@/components/common/custom-table-sort.vue';
import CustomDatePicker from '@/components/common/datePicker.vue';
import CustomAreaSelect from '@/components/common/area-selecter.vue';
import downloadModal from '@/components/common/download-modal.vue';
import RunBall from '@/components/common/run-ball.vue'; // 抛出动画

import {areaLevelCorAreaId} from '@/common/fn/index.js';
import {getStorage, SessionStorage} from '@/utils/storage.js';
import {downloadExcel} from '@/utils/utils.js';

let farmRecordSessionStorage = '';

export default {
	data() {
		return {
			currentPage: 1,
			pageSize: 15,
			total: 1,
			keyword: '',
			farmRecordList: [],
			// 经营类目
			businessCategoryList: [
				{
					bizCategoryName: '全部经营类目',
					id: ''
				}
			],
			businessCategory: '全部经营类目',
			dateValue: '',
			currentSelectStartTime: '',
			pickerOptions: {
				disabledDate: time => {
					// console.log('time=======>', time.getDate(), this.dateValue)
					if (this.currentSelectStartTime) {
						// 只能选择在面板点击的开始时间到前后一年的范围
						let curDate = this.currentSelectStartTime.getTime();
						let oneYear = 365 * 24 * 3600 * 1000;
						// 后一年
						let afterYear = curDate + oneYear;
						// 往前一年
						let beforeYear = curDate - oneYear;
						return time.getTime() > new Date().getTime() || ((time.getTime() > afterYear || time.getTime() < curDate) && (time.getTime() < beforeYear || time.getTime() > curDate));
					} else {
						return time.getTime() > new Date().getTime();
					}
				},
				onPick: time => {
					// 选择当前开始的时间
					this.currentSelectStartTime = time.minDate;
				}
			},
			areaValue: {
				ids: [],
				level: ''
			},
			startAreaId: '',
			defaultSort: 'descending',
			tableHeight: 0,
			loading: false,
			countsFlag: '1',
			// titleStyle: {
			// 	fontSize: '14px',
			// 	fontWeight: '400',
			// 	color: '#072138'
			// },
			excelLoading: false,
			isModalShow: false,
			isRunBall: false,
			isAddNum: false
		};
	},
	beforeRouteEnter(to, from, next) {
		if (from.name === 'farmdetail') {
			farmRecordSessionStorage = SessionStorage.get('farmRecordSessionStorage');
		} else {
			farmRecordSessionStorage = '';
		}
		next();
	},
	created() {
		if (farmRecordSessionStorage) {
			this.keyword = farmRecordSessionStorage.keyword;
			this.pageSize = farmRecordSessionStorage.pageSize;
			this.currentPage = farmRecordSessionStorage.currentPage;
			this.businessCategory = farmRecordSessionStorage.businessCategory;
			this.dateValue = farmRecordSessionStorage.dateValue;
		} else {
			this.keyword = '';
			this.pageSize = 15;
			this.currentPage = 1;
			this.businessCategory = '全部经营类目';
			this.dateValue = '';
		}
		this.getBusinessCategory();
		this.getFarmRecordList();
	},
	mounted() {
		this.$nextTick(() => {
			this.tableHeight = this.$refs.listContent.offsetHeight;
		});
		window.addEventListener('resize', this.resizeHandle, false);
	},
	beforeRouteLeave(to, from, next) {
		// 如果进入farm详情页，缓存过滤条件，用于从详情页返回保持过滤条件
		if (to.name === 'farmdetail') {
			SessionStorage.set('farmRecordSessionStorage', {
				keyword: this.keyword,
				pageSize: this.pageSize,
				currentPage: this.currentPage,
				businessCategory: this.businessCategory,
				dateValue: this.dateValue
			});
		}
		next();
	},
	computed: {
		// 监管层级
		level() {
			return getStorage('govGovernmentSubjectVO').subjectLevel;
		},
		// 监管地区id
		startId() {
			let storage = getStorage('govGovernmentSubjectVO');
			let level = getStorage('govGovernmentSubjectVO').subjectLevel;

			if (level === 1) {
				return storage.provinceId;
			}
			if (level === 2) {
				return storage.cityId;
			}
			if (level === 3) {
				return storage.countyId;
			}
		}
	},
	methods: {
		back() {
			var tempPath = this.$route.path;
            this.$router.go(-1);
            setTimeout(() => {
                if (tempPath === this.$route.path) {
                    this.back();
                }
            }, 200);
		},
		// areaChange() {
		// 	this.currentPage = 1
		// },
		// selectFarmCategory() {

		// },
		goFarm(row) {
			this.$router.push(`/farmdetail/${row.farmId}`);
		},
		async download() {
			// if (!this.isAddNum) {
			// 	this.isRunBall = true;
			// 	setTimeout(() => {
			// 		this.isAddNum = true;
			// 	}, 800)
			// } else {
			// 	this.isModalShow = true;
			// }
			const defaultArea = areaLevelCorAreaId();
			const userInfo = getStorage('govGovernmentUserVO');

			const endTime = this.dateValue.endTime ? this.dateValue.endTime : this.formatDate(new Date().getTime(), '-');
			const startTime = this.dateValue.startTime ? this.dateValue.startTime : this.formatDate(new Date().getTime(), '-', 'startTime');
			const options = {
				farmAreaId: this.areaValue.ids.length ? this.areaValue.ids : [defaultArea.farmAreaId],
				farmAreaType: this.areaValue.level ? this.areaValue.level : defaultArea.farmAreaType,
				farmName: this.keyword,
				endTime: endTime,
				startTime: startTime,
				categoryName: !this.businessCategory || this.businessCategory === '全部经营类目' ? '' : this.businessCategory,
				townId: this.areaValue.townId ? this.areaValue.townId : null,
				villageId: this.areaValue.villageId ? this.areaValue.villageId : null,
				govUserId: userInfo.id
			}

			const res = await this.$ajax.post(this.$api.downloadExcel.listFarmOperationUploadToServer, options);

			// console.log('res=======>', res);
			if (res.data.respCode === 0) {
				if (this.isAddNum) {
					this.isAddNum = false;
				}
				this.isRunBall = true;
				setTimeout(() => {
					this.isRunBall = false;
					this.isAddNum = true;
				}, 800);
			}
			// this.excelLoading = true;
			// const defaultArea = areaLevelCorAreaId();

			// const endTime = this.dateValue.endTime ? this.dateValue.endTime : this.formatDate(new Date().getTime(), '-');
			// const startTime = this.dateValue.startTime ? this.dateValue.startTime : this.formatDate(new Date().getTime() - 1000 * 60 * 60 * 24 * 30, '-');

			// let filename = `农事填报汇总（${startTime.replace(/-/g, '.')}-${endTime.replace(/-/g, '.')}）`;

			// const res = await downloadExcel(
			// 	'/freetekApi/produce/gov/farm/listFarmOperationDownLoad',
			// 	{
			// 		farmAreaId: this.areaValue.ids.length ? this.areaValue.ids : [defaultArea.farmAreaId],
			// 		farmAreaType: this.areaValue.level ? this.areaValue.level : defaultArea.farmAreaType,
			// 		farmName: this.keyword,
			// 		endTime: endTime,
			// 		startTime: startTime,
			// 		categoryName: !this.businessCategory || this.businessCategory === '全部经营类目' ? '' : this.businessCategory,
			// 		townId: this.areaValue.townId ? this.areaValue.townId : null,
			// 		villageId: this.areaValue.villageId ? this.areaValue.villageId : null
			// 	},
			// 	filename
			// );
			// if (res.msg === 'success') {
			// 	this.$message({
			// 		type: 'success',
			// 		message: '下载成功',
			// 		center: true
			// 	});
			// 	this.excelLoading = false;
			// } else {
			// 	this.excelLoading = false;
			// }
		},
		async areaCilck(val) {
			this.areaValue = val;
			this.currentPage = 1;
			this.getFarmRecordList();
		},
		businessChange() {
			this.currentPage = 1;
			this.getFarmRecordList();
		},
		dateChange() {
			this.currentPage = 1;
			this.getFarmRecordList();
		},
		focus() {
			// 时间选择器focus时清空上次选择的限制
			this.currentSelectStartTime = '';
		},
		onChange(page) {
			this.currentPage = page;
			this.getFarmRecordList();
		},
		changePageSize(pageSize) {
			this.pageSize = pageSize;
			this.currentPage = 1;
			this.getFarmRecordList();
		},
		clickAndEnter() {
			this.currentPage = 1;
			this.getFarmRecordList();
		},
		sortChange(value) {
			this.currentPage = 1;
			this.defaultSort = value.order === 'descending' ? 'descending' : 'ascending';
			this.countsFlag = value.order === 'descending' ? 1 : 0;
			this.getFarmRecordList();
		},
		async getFarmRecordList() {
			this.farmRecordList = [];
			this.loading = true;
			const defaultArea = areaLevelCorAreaId();

			if (!this.dateValue) {
				this.dateValue = {};
				this.dateValue.endTime = this.formatDate(new Date().getTime(), '-');
				this.dateValue.startTime = this.formatDate(new Date().getTime(), '-', 'startTime');
			}

			const options = {
				farmAreaId: this.areaValue.ids.length ? this.areaValue.ids : [defaultArea.farmAreaId],
				farmAreaType: this.areaValue.level ? this.areaValue.level : defaultArea.farmAreaType,
				farmName: this.keyword ? this.keyword : '',
				pageNo: this.currentPage,
				pageSize: this.pageSize,
				categoryName: !this.businessCategory || this.businessCategory === '全部经营类目' ? '' : this.businessCategory,
				endTime: this.dateValue.endTime,
				startTime: this.dateValue.startTime,
				townId: this.areaValue.townId ? this.areaValue.townId : null,
				villageId: this.areaValue.villageId ? this.areaValue.villageId : null
			};

			const res = await this.$ajax.post(this.$api.produce.listFarmOperation, options);

			if (res.data.respCode === 0 && res.data.obj) {
				this.farmRecordList = res.data.obj.vos;
				this.total = res.data.obj.count;
				this.loading = false;

				this.$nextTick(() => {
					this.tableHeight = this.$refs.listContent.offsetHeight;
					this.backTop();
				});
			} else {
				this.loading = false;
			}
		},
		async getBusinessCategory() {
			const res = await this.$ajax.post(this.$api.produce.businessCategory, {});

			if (res.data.respCode === 0) {
				res.data.obj.list.forEach(ele => {
					this.businessCategoryList.push(ele);
				});
			}
		},
		spliceAreaName(row) {
			const storage = getStorage('govGovernmentSubjectVO');

			let str = '';
			str = row.townName ? str + '/' + row.townName : '';
			str = row.villageName ? (str ? str + '/' + row.villageName : row.villageName) : str ? str : '';

			if (storage.subjectLevel === 1) {
				return row.cityName + '/' + row.countyName + str;
			}

			if (storage.subjectLevel === 2 || storage.subjectLevel === 3) {
				let str1 = row.cityName;
				str = row.countyName ? str1 + '/' + row.countyName + str : str;

				return str;
			}
		},
		formatDate(time, character, startTime) {
			const date = new Date(time);
			let DD = '';
			const YY = date.getFullYear() + character;
			const MM = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + character;
			if (!startTime) {
				DD = date.getDate() < 10 ? '0' + date.getDate() : date.getDate();
			} else {
				DD = '01';
			}

			return YY + MM + DD;
		},
		resizeHandle() {
			this.tableHeight = this.$refs.listContent.offsetHeight;
		},
		backTop() {
			document.querySelector('.el-table__body-wrapper').scrollTop = 0;
		},
		// 解决element ui tooltip点击后，切换页面回来后，tip出现的问题
		buttonClick(e) {
			e.target.blur();
		}
	},
	destroyed() {
		window.removeEventListener('resize', this.resizeHandle, false);
	},
	components: {
		Nav,
		Page,
		Loading,
		CustomInput,
		CustomSelect,
		CustomTable,
		CustomTableSort,
		CustomDatePicker,
		CustomAreaSelect,
		downloadModal,
		RunBall
	}
};
</script>

<style scoped lang="scss">
.farm-record {
	overflow: hidden;
	display: flex;
	flex-direction: column;
	height: 100vh;
	min-width: 1366px;
	min-height: 696px;
	box-sizing: border-box;
	background-color: #021220;

	.main {
		flex: 1;
		display: flex;
		flex-direction: column;
		height: 100%;
		box-sizing: border-box;
		overflow: hidden;

		.search-box {
			display: flex;
			justify-content: space-between;
			padding: 28px 50px 21px;

			.search-box-left {
				flex: 1;
				display: flex;
			}

			.excel-box {
				display: flex;
				align-items: center;

				.excel {
					display: flex;
					align-items: center;
					justify-content: center;
					width: 120px;
					height: 36px;
					margin-left: 15px;
					border: 1px solid #145763;
					border-radius: 2px;
					background-color: #24969a;
					color: #d9e3ed;
					font-size: 14px;
					cursor: pointer;
				}
			}
		}

		.list-content {
			flex: 1;
			display: flex;
			flex-direction: column;
			overflow: hidden;
			height: 100%;
		}
	}

	.handle {
		display: flex;
		align-items: center;
		font-size: 14px;
		color: #20a8f8;
		cursor: pointer;

		.iconfont {
			font-size: 23px;
		}
	}
	.handle.handle-disabled {
		color: #aaaaaa;
		cursor: not-allowed;
	}

	.custom-header-text {
		font-size: 14px;
		font-family: Microsoft YaHei;
		font-weight: 400;
		color: #24969a;
	}
	.text-ellipsis {
		word-break: break-all;
		text-overflow: ellipsis;
		overflow: hidden;
		display: -webkit-box;
		-webkit-line-clamp: 2;
		-webkit-box-orient: vertical;
		outline: none;
	}
	.text {
		color: #a1acb7;
		font-size: 14px;
	}
	.text-bold {
		color: #a1acb7;
		font-weight: bold;
		font-size: 18px;
	}
	.text-hover {
		color: #1d9096;
	}
	.text-hover:hover {
		border-bottom: 1px solid #1d9096;
	}
	.text-blue {
		color: #1d9096;
	}
	.text-green {
		color: #16b665;
	}
	.value-text {
		color: #a1acb7;
		font-weight: bold;
		font-size: 14px;
	}

	.page-box {
		display: flex;
		justify-content: flex-end;
		align-items: center;
		height: 88px;
		padding: 0 40px;
		background: #021220;
		box-shadow: 0px -3px 21px 0px rgba(4, 52, 82, 0.2);
	}

	.no-data {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
}
</style>

<style lang="scss">
.farm-record {
	.custom-table {
		/deep/ .el-table {
			.el-table__header {
				// padding-right: 20px;
			}

			tr td.el-table_1_column_6_column_7,
			tr:nth-child(2) th:nth-child(1) {
				padding-left: 0;
			}
			thead tr:nth-child(1) th:nth-child(5) {
				border-bottom: 1px solid #010b14;
			}

			thead tr:nth-child(1) th:nth-child(1),
			tbody tr td:nth-child(1) {
				padding-right: 15px;
			}
			thead tr:nth-child(1) th:nth-child(2),
			thead tr:nth-child(1) th:nth-child(3),
			thead tr:nth-child(1) th:nth-child(4) {
				padding: 10px 20px;
			}

			tbody tr td:nth-child(2),
			tbody tr td:nth-child(3),
			tbody tr td:nth-child(4) {
				padding: 10px 20px;
			}

			thead tr th,
			tbody tr td {
				border-right: 1px solid #010b14;
			}
			tbody tr:last-child td {
				border-bottom: 1px solid #010b14;
			}
			thead tr:nth-child(2) th {
				padding: 5px 0;
			}
		}
	}
}
.farm-record-custom-tooltip-light.el-tooltip__popper {
	width: 200px;
	// overflow-y: scroll;
	background-color: #f4f7f9 !important;
	box-shadow: 0px 3px 9px 0px rgba(0, 0, 0, 0.13) !important;
	color: #485256;
	line-height: 24px;

	.popper__arrow {
		bottom: -12px;
		border-color: transparent !important;
		border-top-color: #f4f7f9 !important;
		border-width: 6px !important;
		border-style: solid !important;
	}
	.tooltip-box {
		max-height: 160px;
		overflow: hidden;
		overflow-y: auto;
	}
}
.farm-record-custom-tooltip-dark.el-tooltip__popper {
	.popper__arrow {
		bottom: -12px;
		border-color: transparent !important;
		border-top-color: rgba(0, 0, 0, 0.9) !important;
		border-width: 6px !important;
		border-style: solid !important;
	}
	.tooltip-box {
		max-height: 160px;
		overflow: hidden;
		overflow-y: auto;
	}
}
.farm-record > .el-loading-mask {
	i {
		color: #00e5ff;
		font-size: 40px;
	}
	p {
		color: #00e5ff;
		line-height: 40px;
		font-size: 18px;
	}
}
</style>



